.contact {
  &-us {
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;

    color: #fff;
    margin-bottom: 48px;
  }

  &__block {
    text-decoration: none;
  }

  &__element {
    &:nth-child(odd) {
      font-weight: 700;
      font-size: 13px;
      line-height: 20px;
      letter-spacing: 3px;
      text-transform: uppercase;
      color: #fff;

      opacity: 0.5;
      margin-bottom: 8px;
    }

    &:nth-child(even) {
      font-weight: 600;
      font-size: 24px;
      line-height: 150%;
      color: #fff;
      margin-bottom: 40px;
    }
  }

  &__phone {
    color: inherit;
    @include hover (color, rgb(184, 184, 184));
  }

  &__map {
    color: inherit;
    @include hover (color, rgb(184, 184, 184));
  }

  &__social {
    display: flex;
    gap: 32px;

    &__facebook {
      @include social_image("../images/sub-title/facebook.svg");
      @include upper;
    }

    &__twitter {
      @include social_image("../images/sub-title/twitter.svg");
      @include upper;
    }

    &__instagram {
      @include social_image("../images/sub-title/insta.svg");
      @include upper;
    }
  }
}
